@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
:root {
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --instagram-top: hsl(329, 70%, 58%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-bottom: hsl(37, 97%, 70%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear-gradient(225deg, var(--instagram-top) 0%, var(--instagram-middle) 50.91%, var(--instagram-bottom) 100%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-top: hsl(210, 78%, 56%);
  --toggle-bottom: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-blue-bg: hsl(230, 17%, 14%);
  --dark-blue-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-text: hsl(228, 34%, 66%);
  --dark-text: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-blue-top-bg: hsl(225, 100%, 98%);
  --light-blue-card-bg: hsl(227, 47%, 96%);
  --light-card-text: hsl(228, 12%, 44%);
  --light-text: hsl(230, 17%, 14%);
  --card-fb: .25rem solid var(--facebook);
  --card-tw: .25rem solid var(--twitter);
  --card-in: .25rem solid transparent;
  --card-yt: .25rem solid var(--youtube);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font);
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

main {
  display: flex;
  flex-direction: column;
  gap: 2.875rem;
}

:root {
  --font: "Inter", sans-serif;
  --fsize: .875rem;
  --lg-fsize: 3.5rem;
  --md-fsize: 2rem;
  --md-mfsize: 1.5rem;
  --md-dfsize: 1.75rem;
  --xs-fsize: .75rem;
  --lg-lheight: 3rem;
  --md-lheight: 2.4375rem;
  --md-dlheight: 2.125rem;
  --md-mlheight: 1.8125rem;
  --sm-lheight: 1.0625rem;
  --xs-lheight: .9375rem;
  --bold: 700;
  --regular: 400;
}

/* Dashboard numbers varied size -------------------*/
/* Card metrics (followers, subscribers, etc.) and userNames */
/* h1, h2 and paragraph styles for desktop and mobile -----------------*/
h1,
h2 {
  font-weight: var(--bold);
  font-size: var(--md-mfsize);
  line-height: var(--md-mlheight);
}
@media (min-width: 1440px) {
  h1,
  h2 {
    font-weight: var(--bold);
    font-size: var(--md-dfsize);
    line-height: var(--md-dlheight);
  }
}

p {
  font-weight: var(--bold);
  font-size: var(--fsize);
  line-height: var(--sm-lheight);
}

.facebook {
  border-top: var(--card-fb);
}

.twitter {
  border-top: var(--card-tw);
}

.instagram {
  border-top: var(--card-in);
  border-image-source: linear-gradient(225deg, var(--instagram-top) 0%, var(--instagram-middle) 50.91%, var(--instagram-bottom) 100%);
  border-image-slice: 100% 0 0 0;
}

.youtube {
  border-top: var(--card-yt);
}

.positive {
  font-weight: var(--bold);
  font-size: var(--xs-fsize);
  line-height: var(--xs-lheight);
  color: var(--limeGreen);
}

.negative {
  font-weight: var(--bold);
  font-size: var(--xs-fsize);
  line-height: var(--xs-lheight);
  color: var(--brightRed);
}

.card {
  padding: 2rem 6.1875rem 1.5rem 6.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--light-blue-card-bg);
  border-radius: 0.3125rem;
}
.card__social {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5rem;
  padding-bottom: 1.75rem;
}
.card__social--usrName {
  font-weight: var(--bold);
  font-size: var(--xs-fsize);
  line-height: var(--xs-lheight);
  color: var(--light-card-text);
}
.card .card__num {
  text-align: center;
  padding-bottom: 1.5625rem;
}
.card .card__num--lg {
  display: block;
  font-weight: var(--bold);
  font-size: var(--lg-fsize);
  line-height: var(--lg-lheight);
  color: var(--light-text);
  padding-bottom: 0.5625rem;
}
.card .card__num--metric {
  font-weight: var(--regular);
  font-size: var(--xs-fsize);
  line-height: var(--sm-lheight);
  text-transform: uppercase;
  color: var(--light-card-text);
  letter-spacing: 0.3125rem;
}
.card__trend {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.25rem;
}

.mdCard {
  padding: 1.625rem 1.875rem 1.1875rem 1.5rem;
  background-color: var(--light-blue-card-bg);
  border-radius: 0.3125rem;
}
.mdCard__metrics {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 1.4375rem;
}
.mdCard__metrics--md {
  font-weight: var(--bold);
  font-size: var(--fsize);
  line-height: var(--sm-lheight);
}
.mdCard__num {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
}
.mdCard__num--lg {
  font-weight: var(--bold);
  font-size: var(--md-fsize);
  line-height: var(--md-lheight);
}
.mdCard__num--stats {
  display: flex;
  align-items: center;
  gap: 0.1875rem;
}

.container {
  padding: 0 1.5rem 0 1.5625rem;
  max-width: 68.75rem;
}

header {
  position: relative;
}
header .header {
  display: flex;
  flex-direction: column;
  padding-top: 2.25rem;
  padding-bottom: 1.5rem;
  border-bottom: 0.0625rem solid hsl(229, 19%, 59%);
}
header .header > h1 {
  color: var(--dark-blue-bg);
  padding-bottom: 0.25rem;
}
header .header > p {
  color: var(--light-card-text);
}
header .toggle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
}
header .toggle label {
  font-weight: 700;
  font-size: var(--fsize);
  line-height: var(--sm-lheight);
  color: var(--light-card-text);
}
header .toggle .toggle__input {
  position: relative;
  width: 3rem;
  height: 1.5rem;
  appearance: none;
  background-color: var(--toggle);
  border-radius: 0.75rem;
  outline: none;
  cursor: pointer;
}
header .toggle .toggle__input::before {
  content: "";
  position: absolute;
  top: 0.125rem;
  right: 0.125rem;
  width: 1.25rem;
  height: 1.25rem;
  background-color: var(--light-blue-card-bg);
  border-radius: 50%;
  transition: transform 0.2s ease-in-out;
}
header .toggle .toggle__input:checked::before {
  transform: translateX(-1.5rem);
}

header::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  width: 100%;
  height: 14.6875rem;
  background-color: var(--light-blue-top-bg);
  border-radius: 0 0 1.25rem 1.25rem;
}

.lgCards {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding-top: 2.5rem;
}

.mdCards {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-bottom: 2.8125rem;
}
.mdCards h2 {
  color: var(--light-card-text);
  padding-bottom: 0.6875rem;
}